//待办及提醒事项查看全部详情
import { Component, OnInit, Inject, forwardRef, OnChanges } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import * as $ from 'jquery';
@Component({
	selector: 'app-backlog-detail',
	templateUrl: './backlog-detail.component.html',
	styleUrls: ['./backlog-detail.component.scss'],
	providers: [Commfunc]
})
export class BacklogDetailComponent implements OnInit, OnChanges {
	user: any;
	public show: any = 1;
	mission: string;//是否管理岗的传递标识
	constructor(
		public commfunc: Commfunc,
		public router: Router,
		public activatedRouter: ActivatedRoute,
	) {
		this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
		// 是否为管理岗
		if (this.user.extParam.managePermission == 'Y') {
			this.mission = '1';
		} else {
			this.mission = '2';
		}
	}


	ngOnInit() {
		this.judgStatu();
	}
	ngOnChanges() {
		this.ngOnInit();
	}
	apply(num) {
		this.show = num;
		if (this.commfunc.getSessionDataCH('workliuCode')) {
			this.commfunc.clearSession('workliuCode');
			this.commfunc.clearSession('workliuData');
		}
		if (this.commfunc.getSessionDataCH('bussinessWhere')) {
			this.commfunc.clearSession('bussinessWhere');
		}
	}
	worknone: boolean = false;
	judgStatu() {
		let url = window.location.href
		if (url.indexOf('?') != -1) {
			let a = url.split('?');
			if (a[0].indexOf('backlog-details') == -1) {
				if (this.commfunc.getSessionDataCH('month')) {
					this.commfunc.clearSession('month');
				}
			}
			let b = a[1].split('&');
			let c = b[0].split('=');
			this.show = c[1];
			if (b[2]) {
				let d = b[2].split('=');
				if (d[1] == 'Y') {
					this.worknone = true;
					this.activatedRouter.queryParams.subscribe(params => {
						if (params.startDate) {
							let arr = params.startDate.split('.');
							let arr1 = params.endDate.split('.');
							let session = window.sessionStorage;
							let arrlist = {
								startDate: arr[0] + '-' + arr[1] + '-' + arr[2],
								endDate: arr1[0] + '-' + arr1[1] + '-' + arr1[2]
							}
							session.setItem('month', JSON.stringify(arrlist));
						}
					})
				}
			}
		}
	}
	//按钮权限
	permissionCheck(id) {
		return this.commfunc.permissionCheck(id)
	}
}
