import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { BacklogDetailComponent } from './backlog-detail.component';
export const routes: Routes = [
    {
        path: '',
        component: BacklogDetailComponent,
        children: [
            {
                path: 'btn-page',
                loadChildren: './btn-page/btn-page.module#BtnPageModule'
            }, {
                path: 'btn-page2',
                loadChildren: './btn-page/btn-page.module#BtnPageModule'
            }, {
                path: 'btn-page3',
                loadChildren: './btn-page/btn-page.module#BtnPageModule'
            }, {
                path: 'btn-page4',
                loadChildren: './btn-page/btn-page.module#BtnPageModule'
            }, {
                path: 'btn-page5',
                loadChildren: './btn-page/btn-page.module#BtnPageModule'
            }, {
                path: 'btn-page6',
                loadChildren: './btn-page/btn-page.module#BtnPageModule'
            }, {
                path: 'btn-page7',
                loadChildren: './btn-page/btn-page.module#BtnPageModule'
            }, {
                path: 'btn-page8',
                loadChildren: './btn-page/btn-page.module#BtnPageModule'
            }, {
                path: 'btn-page9',
                loadChildren: './btn-page/btn-page.module#BtnPageModule'
            }, {
                path: 'btn-page10',
                loadChildren: './btn-page/btn-page.module#BtnPageModule'
            },
        ]
    }
]

export const BackLogDetailRouting: ModuleWithProviders = RouterModule.forChild(routes);
