import { Component, OnInit } from '@angular/core';
import { CusOperationHttpService } from '../../../../http/custom-operation/custom-operation.http.service';//事项类型的接口
import { ActivatedRoute, Router } from '@angular/router';
import { BtnPageBean } from '../bean/btnPage.bean';
import { Message } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { TZB_HTTP_XIN, TZB_HTTP_XIN_C } from 'app/@uisftech/common/service/http.constant';
import { CustomeHttpService } from 'app/@uisftech/view/http/uisftech-view.http.service';
import { tableMessage } from 'app/@uisftech/common/constant/config';
/**
 * 审批事项
 */
@Component({
  selector: 'credit-page',
  templateUrl: './credit-page.component.html',
  styleUrls: ['./credit-page.component.scss'],
  providers: [CusOperationHttpService, CustomeHttpService, Commfunc]
})
export class CreditPageComponent implements OnInit {
  //表格中无数据时显示的内容
  tabMesg: any = tableMessage;
  littleEvent: any = [];
  scheduleTypeParentId: string;
  btnPageBean: BtnPageBean = new BtnPageBean();
  itemsSmallType: any = [
  ];
  list: any = [];//接收后台数据
  //提示信息
  msgs: Message[] = [];
  //总页数
  total: number;
  //每页多少条
  pageSize: number;
  pageNum: number;
  userId: string = '';
  targetId: string;
  display: boolean = false;
  showModel: string;
  inValue: any;
  first: any;//分页控制
  //获取所有码值
  code: any;
  //信贷工作任务链接
  HTTPIPDress: any;

  constructor(
    public router: Router,
    public routeInfo: ActivatedRoute,
    public cusOperationHttpService: CusOperationHttpService,
    public customeHttpService: CustomeHttpService,
    public activatedRouter: ActivatedRoute,
    public commfunc: Commfunc,
  ) {
    this.codeValues() //调用方法，获取全部码值
    //获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
    this.littleEvent = this.code['ScheduleType'];
  }

  ngOnInit() {
 
    this.btnPageBean.scheduleTypeId = this.activatedRouter.snapshot.params['scheduleTypeId'];
    // this.itemsBigChange();
    this.btnPageBean.scheduleName = this.activatedRouter.snapshot.params['scheduleName'];
    // this.queryClick();
    this.doQuery();

  }

  //点击查询
  doQuery() {
    let params = {

    };
    this.customeHttpService.loanScheduleCountByTypeQry(params).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.list = data.resultList;
        this.total = data.resultList[0].num;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    })
  }

  //事项大类切换
  // itemsBigChange() {
  //   let params = {

  //   }
  //   this.cusOperationHttpService.taskTypeQry(params).subscribe(data => {
  //     let arr = data.taskTypeList;
  //     this.itemsSmallType = [{ label: '请选择', value: '' }];
  //     if (arr) {
  //       arr.forEach(item => {
  //         this.itemsSmallType.push({ label: item.taskTypeName, value: item.taskTypeId });

  //       });
  //     }
  //   })
  // }

  //信贷工作任务处理
  toDisposeSchedule(item) {
    //新建时间戳和页面
    let timestamp = new Date().getTime();
    let target = "custLinkForm" + timestamp;
    this.commfunc.createBlank(target);

    //审批事项中的信贷
    let user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    this.userId = user.userId;
    this.targetId = user.targetId;
    let params = {
      userId: this.userId,
      sysCode: '122',
      casServerLoginUrl: 'http://10.1.39.140:8011/searchPaymentTool',
      serverName: 'ywsn',
      validateType: '0',
      extraInfo: 'ywsn',
      encryptType: 'HS256',
    }
    this.customeHttpService.tokenApply(params).subscribe(data => {
      if (data.returnCode.code == 'success') {
        let token = data.tokenCode;
        if (this.targetId == '101010') {
          this.HTTPIPDress = TZB_HTTP_XIN;
        } else if (this.targetId == '101011') {
          this.HTTPIPDress = TZB_HTTP_XIN_C;
        }
        let url = this.HTTPIPDress + 'plats/FrontController?urlParam=' + item.url + '&suid=' + this.userId + '&token=' + token;
        // console.log(url)
        let features = 'top=80,left=50,width=1180,height=768,toolbar=no,location=no,directories=0,status=1,menubar=no,scrollbars=yes,resizable=yes';
        let closeOnLoseFocus = 'closeOnLoseFocus';
        this.commfunc.openPostWindow(url, "custLinkForm" + timestamp, features, closeOnLoseFocus);
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
    })

  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }

}
